<template>
  <div id="app" class="container">
    <header class="header clearfix">
      <nav>
        <ul class="nav nav-pills float-right">
          <li class="nav-item">
            <router-link to="/login" class="nav-link">Login</router-link>
          </li>
          <li class="nav-item">
            <a href="#" class="nav-link text-muted">or</a>
          </li>
          <li class="nav-item">
            <router-link to="/register" class="nav-link nav-active">Sign up →</router-link>
          </li>
        </ul>
      </nav>
      <h2 class="text"><router-link to="/">Tracker ⏱</router-link></h2>
    </header>
    <div class="row" v-if="flash">
      <p v-text="flash" v-class="flashClass"></p>
    </div>
    <router-view/>
  </div>
</template>

<script>
export default {
  name: 'app',
  data() {
    return {
      flash: "",
      flashClass: ""
    }
  }
}
</script>

<style>
#app {
  margin-top: 20px;
}

.loader,
.loader:after {
  border-radius: 50%;
  width: 3.6em;
  height: 3.6em;
}
.loader {
  display: block;
  margin: 3em auto;
  font-size: 10px;
  position: relative;
  text-indent: -9999em;
  border-top: 0.5em solid rgba(0,121,251, 0.2);
  border-right: 0.5em solid rgba(0,121,251, 0.2);
  border-bottom: 0.5em solid rgba(0,121,251, 0.2);
  border-left: 0.5em solid #0079fb;
  -webkit-transform: translateZ(0);
  -ms-transform: translateZ(0);
  transform: translateZ(0);
  -webkit-animation: load8 1.1s infinite linear;
  animation: load8 1.1s infinite linear;
}
@-webkit-keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}
@keyframes load8 {
  0% {
    -webkit-transform: rotate(0deg);
    transform: rotate(0deg);
  }
  100% {
    -webkit-transform: rotate(360deg);
    transform: rotate(360deg);
  }
}

</style>



